.tab-content-wrapper {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 0 -5px;
    .content-wrapper {
        padding: 10px;
        min-width: 25%;
        height: 200px;
        width: 25%;
        .content {
            background-color: rgba($color: #fff, $alpha: 0.10);
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            position: relative;
            border-radius: 4px;
            box-shadow: 0px 0px 4px rgba(#000, 0.10);
            .content-label {
                margin: 0;
                background-color: rgba($color: #fff, $alpha: 0.10);
                line-height: 16px;
                font-size: xx-small;
                padding: 2px 4px;
                letter-spacing: 2px;
                text-transform: uppercase;
                position: absolute;
                top: 0;
                left: 0;
                opacity: 0.50;
            }
            h1 {
                opacity: 0.50;
                margin: 0;
                padding: 0;
                text-transform: uppercase;
                font-size: medium;
                letter-spacing: 5px;
            }
            h2 {
                opacity: 0.2;
                margin: 0;
                padding: 0;
            }
        }
    }
}